'use strict';

(function (factory) {

    "use strict";

    if (typeof define === 'function' && define['amd']) {
        define(['lib/request', 'lib/url'], factory);
    }
})(function (req, url) {

    "use strict";

    console.log('app/data-context');

    var repos = {
        get: function get(size) {

            size = size || 20;

            return req.get(url.resolveApi('/api/repo?size=' + size)).then(function (repos) {

                // Convert it into a format so it is consistant with blog summary
                return _.chain(repos.items).reject(function (item) {

                    return (/(^-DEPRECATED-|lski\.github\.io)/i.test(item.name)
                    );
                }).map(function (item) {

                    return {
                        id: item.id,
                        title: item.name,
                        body: item.description,
                        viewLink: item.html_url,
                        target: '_blank',
                        displayDate: item.updated_at,
                        showInResults: true
                    };
                }).value();
            });
        }
    };

    return {
        repos: repos
    };
});